<template>
  <div class="page page_layout_deposit-transfer">
    <van-tabs v-model="tabActive">
      <van-tab
        title="保证金转入"
        name="deposit_transfer_in"
        to="/deposit_transfer/deposit_transfer_in"
        replace
      />
      <van-tab
        title="保证金转出"
        name="deposit_transfer_out"
        to="/deposit_transfer/deposit_transfer_out"
        replace
      />
    </van-tabs>
    <router-view
      :key="$route.path"
      :hasBottomBar="$root.bottomBarDisplay"
      :hasTopBar="$root.topBarDisplay"
      :isHome="$route.meta.isHome"
      class="ios-pd-tp ios-pd-bt"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: "",
    };
  },
  computed: {
    tabActive: {
      get() {
        return this.$route.path.replace("/deposit_transfer/", "");
      },
      set(val) {
        this.active = val;
      },
    },
  },
  created() {
    this.$root.$emit("root:set-topbar", ["left-back"]);
    this.$root.getCryptoMerchantInfo()
  },
};
</script>

<style lang="scss">
.page_layout_deposit-transfer {
  .page {
    top: 43.98px;
  }
}
